/* 主体 */
$content-bg: #fff !default;

.tw-body {
  position: relative;
  @include make-container($paddingY:true);
  min-height: 450px;
  margin-bottom: $spacing-large;
  background: $content-bg;
}

.tw-body > .tw-multicol {
  margin: -$spacing-medium;
}



