/* --------------------------- 重置teewon框架样式 --------------------------- */
.tw-sidebar:after {
  @include lineY($color-border-auxiliary);
  left: 200px;
}

/* --------------------------- 通用组件 --------------------------- */
/* 内容块 */
$block-ml: $spacing-medium !default;
$block-title-color: $color-text-auxiliary !default;
$block-border-color:$color-border-main !default;

// 内容块列表
.tw-scssvar {
  position: relative;
  display: inline-block;
  padding-bottom: 30px;
  vertical-align: top;
}

.tw-scssvar + .tw-scssvar {
  margin-left: $spacing-large;
}

// 内容块-主体
.tw-scssvar-body {
  position: relative;
  width: 65px;
  height: 65px;
  line-height: 65px;
  text-align: center;
  border: 1px dashed $color-border-main;
}

.tw-scssvar-value {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 100%;
  height: 100%;
  text-align: center;
  text-shadow: 0 1px #fff, 1px 0 #fff, -1px 0 #fff, 0 -1px #fff;
  vertical-align: top;
  border: none;
}

// 内容块-标题
.tw-scssvar-title {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  line-height: 30px;
  color: $block-title-color;
  text-align: center;
  white-space: nowrap;
  border-top: none;
}

/* 色板 */
.tw-palette {
  margin-bottom: $spacing-large;
}

.tw-palette > .tw-scssvar.xlml {
  margin-left: $spacing-large * 2;
}

/* 颜色单元 */
.tw-colorcell {
  width: 100%;
  height: 100%;
  vertical-align: top;
  cursor: pointer;
}

// stylelint-disable
.tw-colorcell > .el-color-picker__trigger {
  width: 100%;
  height: 100%;
  padding: 0;
  border: none;
}

.tw-colorcell .el-color-picker__color {
  border: none;
}

.tw-colorcell .el-color-picker__empty,
.tw-colorcell .el-color-picker__icon {
  display: none !important;
}
// stylelint-enable

.tw-addbtn.xscssvar {
  width: 65px;
  height: 65px;
  margin-left: $spacing-large;
}



